<template>
  <div class="main">
    <h1>Criba de Eratóstenes</h1>
    <div id="principal">
      <div id="left">
        <h3>Introduce un número mayor o igual a 20</h3>
        <input type="number" min="20" v-model="number">
        <div id="mult">
          <button @click="mul2">Múltiplos de 2</button>
          <button @click="mul3">Múltiplos de 3</button>
          <button>Múltiplos de 4</button>
          <button>Múltiplos de 5</button>
          <button>Múltiplos de 6</button>
          <button>Múltiplos de 7</button>
          <button>Múltiplos de 8</button>
          <button>Múltiplos de 9</button>
          <button>Números primos</button>
          <button @click="clean">Limpiar</button>
        </div>
      </div>
      <div id="right">
        <Cubo id="cubo" v-for="cubito in printCubes" :key="cubito" :dato="cubito" />
        <!-- <div id="cubo" v-for="num in printCubes" :key="num" :style="[num%2==0 ? {'background-color': 'yellow'} : {}]">{{num}}</div> -->
        <!-- <div id="cubo" v-for="num in printCubes" :key="num" :class="[{'mul2':valor},{'mul3':valor3}]">{{num}}</div> -->
      </div>
    </div>
  </div>
</template>

<script>
import Cubo from "@/components/Cubo.vue";

export default {
  name: "criba",
  data() {
    return {
      number: 20,
      arrayNumbers: [],
      valor2: false,
      valor3: false
    };
  },
  methods: {
    clean() {
      this.number = 20;
      this.arrayNumbers = [];
      
    },
    mul2(){
      this.valor2 = true;
    },
    mul3(){
      this.valor3 = true;
    }
  },
  computed: {
    printCubes(){
      this.arrayNumbers = [];
      if(this.number >= 20){
        for(let i=1; i<=this.number; i++){
          this.arrayNumbers.push(i);
        }
      }else{
          alert("Te daré chance pero escribe uno mayor ¬¬");
      }
      return this.arrayNumbers;
    }
  },
  components: {
    Cubo
  }
};
</script>

